* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  color: #333;
  line-height: 1.6;
}

/* Auth Components */
.auth-box {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 40px;
  width: 90%;
  max-width: 400px;
  margin: 100px auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.auth-title {
  text-align: center;
  margin-bottom: 30px;
  color: #2c3e50;
  font-size: 28px;
  font-weight: 700;
}

.form-group {
  margin-bottom: 20px;
}

.form-input {
  width: 100%;
  padding: 15px 20px;
  border: 2px solid #e1e8ed;
  border-radius: 12px;
  font-size: 16px;
  transition: all 0.3s ease;
  background: #fff;
}

.form-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.btn {
  width: 100%;
  padding: 15px 20px;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.3);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #5a6268;
  transform: translateY(-1px);
}

.btn-small {
  width: auto;
  padding: 8px 16px;
  font-size: 14px;
}

.auth-switch {
  text-align: center;
  margin-top: 20px;
  color: #6c757d;
}

.auth-link {
  color: #667eea;
  cursor: pointer;
  font-weight: 600;
}

.auth-link:hover {
  text-decoration: underline;
}

/* Dashboard */
.dashboard {
  min-height: 100vh;
  background: #f8f9fa;
}

.navbar {
  background: linear-gradient(135deg, #2c3e50, #34495e);
  color: white;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}

.nav-title {
  font-size: 24px;
  font-weight: 700;
}

.nav-user {
  display: flex;
  align-items: center;
  gap: 15px;
}

.logout-btn {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.logout-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.dashboard-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  padding: 30px;
  max-width: 1400px;
  margin: 0 auto;
}

@media (max-width: 1024px) {
  .dashboard-content {
    grid-template-columns: 1fr;
  }
}

.devices-section, .alerts-section, .map-section {
  background: white;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  border: 1px solid #e1e8ed;
}

.devices-section h3, .alerts-section h3, .map-section h3 {
  margin-bottom: 20px;
  color: #2c3e50;
  font-size: 20px;
  font-weight: 600;
  border-bottom: 2px solid #f1f3f4;
  padding-bottom: 10px;
}

/* Device Cards */
.device-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 15px;
  border-left: 4px solid #667eea;
  transition: all 0.3s ease;
}

.device-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.device-card h4 {
  color: #2c3e50;
  margin-bottom: 10px;
  font-size: 18px;
}

.device-status {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 8px;
}

.status-safe {
  background: #d4edda;
  color: #155724;
}

.status-warning {
  background: #fff3cd;
  color: #856404;
}

.status-offline {
  background: #f8d7da;
  color: #721c24;
}

/* Alert Cards */
.alert-card {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 10px;
}

.alert-card strong {
  color: #856404;
}

/* Map Section */
.map-container {
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 15px;
}

.map-legend {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
}

.legend-color {
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.legend-color.green {
  background: #28a745;
}

.legend-color.orange {
  background: #ffc107;
}

.legend-color.red {
  background: #dc3545;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
}

.modal-content {
  background: white;
  border-radius: 20px;
  padding: 30px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
}

.modal-header {
  text-align: center;
  margin-bottom: 25px;
}

.modal-header h2 {
  color: #2c3e50;
  margin-bottom: 10px;
}

.device-info-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.info-grid {
  display: grid;
  gap: 10px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.device-badge {
  background: #667eea;
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
}

.exact-location-section {
  margin: 25px 0;
}

.location-status {
  padding: 15px;
  border-radius: 12px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 500;
}

.location-status-idle {
  background: #e3f2fd;
  color: #1565c0;
}

.location-status-loading {
  background: #fff3e0;
  color: #ef6c00;
}

.location-status-success {
  background: #e8f5e8;
  color: #2e7d32;
}

.location-status-error {
  background: #ffebee;
  color: #c62828;
}

.location-loading {
  display: flex;
  align-items: center;
  gap: 15px;
  justify-content: center;
  padding: 20px;
}

.spinner {
  width: 30px;
  height: 30px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.location-success-details {
  background: #e8f5e8;
  border-radius: 12px;
  padding: 20px;
  margin-top: 15px;
}

.success-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
  color: #2e7d32;
  font-weight: 600;
}

.location-coordinates {
  display: grid;
  gap: 8px;
}

.coordinate-item {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
  border-bottom: 1px solid rgba(46, 125, 50, 0.1);
}

.location-error {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px;
  background: #ffebee;
  border-radius: 12px;
  margin-top: 15px;
}

.error-icon {
  font-size: 24px;
}

.get-location-btn {
  width: 100%;
  margin-bottom: 10px;
}

.mobile-note {
  text-align: center;
  font-size: 12px;
  color: #6c757d;
  margin-top: 8px;
}

.modal-actions {
  display: flex;
  gap: 15px;
  margin: 25px 0;
}

.modal-actions .btn {
  flex: 1;
}

.privacy-note {
  text-align: center;
  color: #6c757d;
  padding-top: 15px;
  border-top: 1px solid #e1e8ed;
}

/* Popup Styles */
.popup-content {
  min-width: 200px;
}

.popup-details {
  margin-top: 8px;
}

.popup-details div {
  margin-bottom: 4px;
  font-size: 14px;
}

/* Custom Direction Icon */
.custom-direction-icon {
  background: transparent !important;
  border: none !important;
}

/* Responsive Design */
@media (max-width: 768px) {
  .dashboard-content {
    padding: 15px;
    gap: 20px;
  }
  
  .navbar {
    padding: 15px 20px;
    flex-direction: column;
    gap: 10px;
  }
  
  .auth-box {
    margin: 50px auto;
    padding: 30px 25px;
  }
  
  .modal-content {
    padding: 20px;
  }
  
  .modal-actions {
    flex-direction: column;
  }
}

/* No Devices State */
.no-devices {
  text-align: center;
  padding: 40px 20px;
  color: #6c757d;
}

.no-devices p {
  font-size: 16px;
}